.pos{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 670px;
    backdrop-filter: blur(6.7px);
    -webkit-backdrop-filter: blur(6.7px);

}
.form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    max-width: 400px;
    /*max-height: 300px;*/
    box-shadow:  -20px 20px 60px #d2d2d3,
    20px -20px 60px #ffffff;
    background: rgba(255, 255, 255, 0.66);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(6.7px);
    -webkit-backdrop-filter: blur(6.7px);
    animation: ani 0.5s forwards;

}
@keyframes ani {
    0% {transform: translateX(-150%);}
    100% {transform: translateY(0);}
}
.formTitle {
    font-size: 1.25rem;
    line-height: 1.75rem;
    font-weight: 600;
    text-align: center;
    color: #000;
}

.inputContainer {
    position: relative;
}

.inputContainer input, .form button {
    outline: none;
    border: 1px solid #e5e7eb;
    margin: 8px 0;
}

.inputContainer input {
    background-color: #fff;
    padding: 1rem;
    padding-right: 3rem;
    font-size: 1.5rem;
    line-height: 1.25rem;
    width: 300px;
    border-radius: 0.5rem;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

.inputContainer span {
    display: grid;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    padding-left: 1rem;
    padding-right: 1rem;
    place-content: center;
}

.inputContainer span svg {
    color: #9CA3AF;
    width: 1rem;
    height: 1rem;
}

.submit {
    display: block;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    background-color: #4F46E5;
    color: #ffffff;
    font-size: 1rem;
    line-height: 1.25rem;
    font-weight: 500;
    /*width: 100%;*/
    border-radius: 0.5rem;
    text-transform: uppercase;
}

.signupLink {
    color: #6B7280;
    font-size: 0.875rem;
    line-height: 1.25rem;
    text-align: center;
}

.signupLink a {
    text-decoration: underline;
}
#btn_close{
    background: transparent linear-gradient(90deg, #fd3029 0%, #fdb530 100%) 0% 0% no-repeat padding-box;;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: none;
    margin-left: 300px;
    padding: 0;
}