.peoples {
    width: 536px;
    height: 468px;
    background: #FFFFFF;
    border-radius: 16px;
    margin-top: 14px;
    padding: 21px;
    overflow-y: scroll;
    overflow-x: auto; /* прокрутка по горизонтали */
}

.peoples::-webkit-scrollbar {
    background: none;
    width: 20px;
}
.peoples::-webkit-scrollbar-thumb {
    /*background: linear-gradient(180deg, #6882EF, #8DA3FF);*/
    background-color: #8DA3FF;
    border-radius: 16px;
}

